*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #65d2ea;
}

.container {
  width: 40%;
  min-width: 390px;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 50px 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h2, h4 {
  text-align: center;
  margin-bottom: 20px;
}

.container * {
  border: none;
  outline: none;
  font-family: Poppins, sans-serif;
}

.inputs-wrapper {
  background-color: #1f6070;
  border-radius: 8px;
  margin-bottom: 50px;
  padding: 30px 25px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, .3);
}

input, button {
  height: 50px;
  color: #080808;
  background-color: #fff;
  border-radius: 5px;
  font-weight: 500;
}

input {
  width: 60%;
  padding: 0 20px;
  font-size: 14px;
}

button {
  width: 30%;
  float: right;
}

.outputs-wrapper {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.outputs-wrapper div {
  height: 100px;
  width: 100px;
  color: #fff;
  background-color: #1f6070;
  border-radius: 5px;
  align-items: center;
  justify-items: center;
  padding: 20px 0;
  display: grid;
  box-shadow: 0 15px 20px rgba(0, 0, 0, .3);
}

span {
  font-size: 30px;
  font-weight: 500;
}

p {
  color: #2af3ef;
  font-size: 14px;
  font-weight: 400;
}

