*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #65d2ea;
}
.container {
  width: 40%;
  min-width: 390px;
  border: 1px solid black;
  border-radius: 5px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  padding: 50px 30px;
}
h2,
h4 {
  text-align: center;
  margin-bottom: 20px;
}
.container * {
  font-family: 'Poppins', sans-serif;
  border: none;
  outline: none;
}
.inputs-wrapper {
  background-color: #1f6070;
  padding: 30px 25px;
  margin-bottom: 50px;
  border-radius: 8px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
}
input,
button {
  height: 50px;
  background-color: #fff;
  color: #080808;
  font-weight: 500;
  border-radius: 5px;
}
input {
  width: 60%;
  padding: 0 20px;
  font-size: 14px;
}
button {
  width: 30%;
  float: right;
}
.outputs-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.outputs-wrapper div {
  display: grid;
  place-items: center;
  height: 100px;
  width: 100px;
  background-color: #1f6070;
  border-radius: 5px;
  color: #fff;
  padding: 20px 0;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
}
span {
  font-size: 30px;
  font-weight: 500;
}
p {
  font-size: 14px;
  font-weight: 400;
  color: #2af3ef;
}
